<template>
  <div id="content_box" v-bind:style= "{marginBottom : lastComponentMargin + 'vw'}" >
      <div id="container" v-bind:style= "{marginBottom : lastComponentTextMargin + 'vw'}">
        <div id="content_wrapper">
            
                <p v-bind:id="info.id" v-bind:style = "{marginRight: webWordAdjustment + 'vw'}">
                    {{info.number}}
                </p>
                
                <div id="content_text" v-bind:style = "{marginLeft: textMarginLeft + 'vw'}">
                    <p v-bind:id="info.headerID" class="content_text_header">
                    {{info.header}} 
                    </p>
                    <div>
                        <p  class="content_text_line">{{info.text}}</p>
                        
                    </div>
                </div>
        </div>
        <div id="content_image">
            <img :src="info.illustration" v-bind:style="{maxWidth : analysisWidth + 'vw'}">
            </div>
    </div>
  </div>
</template>

<script>


export default {
    props: ['info'],
  data() {
    return{
       isService : false,
       analysis :false,
      }
    },
 
  computed : {
      textMarginLeft(){
          
            if(this.info.number==="Web"){
                return 5 //since web-word has different spacing than numbers
            }else if(this.info.number==='3' )
                {return  1
                }},
    webWordAdjustment(){
        if(this.info.header ==='Upkeep' && window.innerWidth<767){
            if(window.innerWidth<461){
                return 51.5
            }
            else if(window.innerWidth<768){
                return 46.5}
        }
    },
    lastComponentMargin(){
        if(this.info.header === 'Upkeep'){
            return 0
        }
    },
    lastComponentTextMargin(){
        if(this.info.header === 'Upkeep'){
            return 7.8125
        }
    },
    analysisWidth () {
        if(this.info.header === 'Analysis'){
            return 43
        }
    }
      },
   

  }  

    
  

</script>

<style scoped>
#content_box{
    font-family: 'Montserrat', 'sans serif';
    display:flex;
    width:100%;
   
    /* margin-bottom: 16rem; */
    justify-content:center;
    margin-bottom: 7.8125vw;
}
#container{
    display:flex;
    flex-direction:row-reverse;
    justify-content:space-between;
    width:85%;
}
#content_wrapper{
    display:flex;
    width:50%;
    align-self:center;
    flex-basis: 37%;
    
}

#content_number{
    font-family: 'open sans', 'sans serif';
    font-size: 12.3046875vw;
    /* font-size: 26.10625rem; */
    color: #4841AA;
    opacity: .12;
    font-weight:700;
    margin: 0;
    margin-top:-3.3125vw;
    margin-right: 1vw;
    /* max-height:0px;
    max-width: 0px; */
}
.content_text_header{
    font-weight: 700;
    font-size: 4.21875vw;
    /* font-size: 9.1875rem; */
    margin: 0;
    
}
.content_text_line{
    font-weight: 100;
    font-size: 1.171875vw;
    /* font-size: 2.25rem; */
    margin: 0;
    color: #969696;
    line-height:1.6;
}
#content_image{
    display:flex;
    justify-content:flex-start;
    flex-shrink:1.5;
    width:42.5%;
}
#content_image img{
    margin-top:-2rem;
    max-width: 31vw;
    max-height: 600px;
}
#web-word{
    transform : rotate(-90deg) ;
    margin: 0px;
    font-size: 3.90625vw;    
    font-weight: 700;
    color: #4841AA;
    opacity: .22;
    letter-spacing: -4px;
    padding: 0px;
    margin-top: 32%;
    max-height:0px;
    max-width: 0px;
}

@media screen and (max-width:1240px){
    #content_wrapper{
        flex-basis: 49%;
    }
    .content_text_header{
    font-size: 5vw;
    margin: 0;
    }

    .content_text_line{
    width: 95%;
    font-weight: 400;
    font-size: 1.5vw;
    opacity: .7;
   
}
#content_number{
    margin-top:-3.48vw;
    font-size: 14.2vw;
   
}
#web-word{
    font-size: 4.9vw;
    margin-top: 29%;
    margin-right:2%;
}
}
@media screen and (max-width:767px){
   #content_box{
       margin-bottom: 16vw;
   }  
   #container{
      flex-direction:column;
      align-items:center;
   }
   #content_wrapper{
       padding: .1px;
       width:100%;
       justify-content:center;
       
   }
   
   #content_text{
       width:100%;
       
   }
   .content_text_header{
       font-size: 10.144vw;
       text-align:center;
       
   }
   .content_text_line{
       margin-top:1vw;
       font-size: 2.657vw;
       text-align:center;
       width:100%;
   }
   #content_number{
       font-size: 10.144vw;
       position:absolute;
       margin-right:53vw;
       margin-top: -.9vw;
   }
   
   #content_image{
        min-width:50vw;
       margin-top: 8vw;
       
       
   }
   #content_image img{
      min-width:50vw;
        max-height:auto;
       margin-top:0;
      
   }
   #web-word{
       position:absolute;
       font-size: 3.7vw;
       margin-top: 11%;
       letter-spacing: -2px;
       margin-right: 42vw;
      
   }
   
}
@media screen and (max-width:460px) {
    .content_text_header{
        font-size:11.5vw;
    }
    .content_text_line{
        font-size: 3.2vw;
    }
    #content_number{
       font-size: 11.5vw;
       margin-right:59vw;
       margin-top: -1vw;
   }
   #web-word{
        font-size: 4.2vw;
        margin-top: 12.4%;
        letter-spacing: -1px;
        margin-right: 47vw;
   }
}
</style>
