<template>
  <div id="content_box">
      <div id="container">
        <div id="content_wrapper">
            
                <p v-bind:id="info.id" v-bind:style = "{marginRight: numberMarginRight + 'vw'}">
                    {{info.number}}
                </p>
                
                <div id="content_text" v-bind:style = "{marginLeft: textMarginLeft + 'vw'}">
                    <p v-bind:id="info.headerID" class="content_text_header">
                    {{info.header}} 
                    </p>
                    <div>
                        <p class="content_text_line">{{info.text}}</p>
                        
                    </div>
                </div>
        </div>
        <div id="content_image">
            <img :src="info.illustration">
            </div>
    </div>
  </div>
</template>

<script>


export default {
    props: ['info'],
  data() {
    return{
       isService : false,
      }
    },
  computed : {
    textMarginLeft(){
          
            if(this.info.number==="Web"){
                return 5 //since web-word has different spacing than numbers
            }else if(this.info.number==='3' )
                {return  1
                }},
    numberMarginRight(){
        if(this.info.number === '3' ){
            if(window.innerWidth<461){
                return 57
            }
            else if(window.innerWidth<768){
                return 51.5}
        }
    }
      }
  }  

    
  

</script>

<style scoped>
